<style lang="less">
@import './styles/vue-bigdata-table.less';
</style>

<template>
	<div class="v-bt-outer" ref="outer" @DOMMouseScroll="handleScroll" @scroll="handleScroll">
		<div :class="wrapperClasses" :style="tableWidthStyles">
			<div class="v-bt-wrapper" ref="outWrapper">
				<div :class="['v-bt-header-wrapper', fixed ? 'header-wrapper-fixed' : '']" :style="headerStyle">
					<slot name="top" :colWidthArr="widthArr"></slot>
					<table v-if="fixedCol >= 0" :class="['v-bt-fixed-header', showFixedBoxShadow ? 'box-shadow' : '']" cellspacing="0" cellpadding="0" border="0">
						<colgroup>
							<col v-if="i <= fixedCol" :width="width" v-for="(width, i) in widthArr" :key="'header-key-fixed-' + i" />
						</colgroup>
						<tr :style="{cursor: cursorOnHeader}" @mousemove.capture.prevent="handleMousemove" @mousedown="handleMousedown" @mouseup="canNotMove" @mouseleave="canNotMove">
							<th v-if="i <= fixedCol" v-for="(col, i) in columnsHandled" :data-index="i" :key="`table-title-${i}`" style="border-right: 1px solid #e9eaec;">
								<span v-if="!col.render">{{ col.title }}
									<sort-button v-if="showSortBtn(i)" :col-index="i" @on-sort="handleSort" @on-cancel-sort="handleCancelSort" :current-sort-col-index="sortedByColIndex" :current-sort-type="sortedType"></sort-button>
								</span>
								<render-dom v-else :render="col.render" :back-value="getComputedTableDataIndex(i)"></render-dom>
							</th>
						</tr>
					</table>
					<table ref="headerTable" style="position: absolute;left: 0;top: 0;" cellspacing="0" cellpadding="0" border="0" width="100%">
						<colgroup>
							<col :width="width" v-for="(width, i) in widthArr" :key="'header-key-' + i" />
						</colgroup>
						<tr :style="{cursor: cursorOnHeader}" @mousemove.capture.prevent="handleMousemove" @mousedown="handleMousedown" @mouseup="canNotMove" @mouseleave="canNotMove">
							<th v-for="(col, i) in columnsHandled" :data-index="i" :key="`table-title-${i}`">
								<span v-if="!col.render && (i > fixedCol)">{{ col.title }}
									<sort-button v-if="showSortBtn(i)" :col-index="i" @on-sort="handleSort" @on-cancel-sort="handleCancelSort" :current-sort-col-index="sortedByColIndex" :current-sort-type="sortedType"></sort-button>
								</span>
								<render-dom v-else-if="(i > fixedCol)" :render="col.render" :back-value="getComputedTableDataIndex(i)"></render-dom>
							</th>
						</tr>
					</table>
				</div>
				<div :class="['v-bt-content', canSelectText ? '' : 'noselect-text']" @mousedown="handleMousedownOnTable">
					<div :style="{height: `${topPlaceholderHeight}px`}"></div>
					<render-dom :render="renderTable"></render-dom>
					<div :style="{height: `${bottomPlaceholderHeight}px`}"></div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import renderDom from './components/renderDom'
import sortButton from './components/sort-button.vue'
import editRender from './components/input-render'
import mixins from './mixins'
export default {
  name: 'bigdataTable',
  components: {
    renderDom,
    sortButton
  },
  mixins: [...mixins],
  props: {
		/**
		 * @description 
		 */
    showIndex: {
      type: Boolean,
      default: false
    },
    rowNum: Number,
    colNum: Number,
		/**
		 * @description 
		 */
    value: {
      type: Array
    },
		/**
		 * @description 
		 */
    rowHeight: {
      type: Number,
      default: 48
    },
		/**
		 * @description 
		 */
    fixed: {
      type: Boolean,
      default: false
    },
		/**
		 * @description trueindexWidth
		 */
    fixedWrapperWidth: {
      type: Boolean,
      default: false
    },
		/**
		 * @description 
		 */
    disabledHover: {
      type: Boolean,
      default: true
    },
		/**
		 * @description { title: 'xxx', render: (h) => {} }
		 */
    columns: {
      type: Array
    },
		/**
		 * @description 
		 */
    colWidth: {
      type: Number,
      default: 100
    },
		/**
		 * @description 
		 */
    headerHeight: {
      type: Number,
      default: 52
    },
		/**
		 * @description tr
		 */
    headerTrStyle: {
      type: Object,
      default: () => {
        return {}
      }
    },
		/**
		 * @description 
		 */
    indexWidth: Number,
		/**
		 * @description render
		 */
    indexRender: {
      type: Function,
      default: (h, index) => {
        return h('span', index + 1)
      }
    },
		/**
		 * @description 
		 */
    stripe: {
      type: Boolean,
      default: false
    },
		/**
		 * @description atLeftCellPosi
		 */
    atLeftCellPosi: {
      type: Number,
      default: 80
    },
		/**
		 * @description atRightCellPosi
		 */
    atRightCellPosi: {
      type: Number,
      default: 80
    },
		/**
		 * @description [0, fixedCol]2012
		 */
    fixedCol: {
      type: Number,
      default: -1
    },
		/**
		 * @description 1/3
		 */
    appendNum: {
      type: Number,
      default: 15
    },
		/**
		 * @description 
		 */
    canEdit: {
      type: Boolean,
      default: false
    },
		/**
		 * @description enum:['dblclick' => ]
		 */
    startEditType: {
      type: String,
      default: 'dblclick'
    },
		/**
		 * @description render
		 */
    editCellRender: {
      type: Function,
      default: editRender
    },
		/**
		 * @description false
		 */
    beforeSave: {
      type: Function,
      default: () => {
        return true
      }
    },
		/**
		 * @description 
		 */
    selectable: {
      type: Boolean,
      default: false
    },
		/**
		 * @description trueselectabletrue
		 */
    paste: {
      type: Boolean,
      default: false
    },
		/**
		 * @description 
		 */
    sortable: {
      type: Boolean,
      default: false
    },
		/**
		 * @description 
		 */
    sortIndex: [Array, Number],
		/**
		 * @description 
		 */
    defaultSort: Object
  },
  data() {
    return {
      prefix: 'v-bt'
    }
  },
  methods: {
		// 
    resize() {
      this._tableResize()
    },
		// 
    getScrollLeft() {
      return this.$refs.outer.scrollLeft
    },
		// 
    scrollToRow(index) {
      this._scrollToIndexRow(index)
    },
		// canEdittruerow+1col+1
    editCell(row, col) {
      this._editCell(row, col)
    }
  },
  watch: {
    value(val) {
      this.$nextTick(() => {
        this.insideTableData = this.setIndex(this.value)
        this.initSort()
        this._initM()
      })
    },
    insideTableData() {
      this.resize()
    },
    defaultSort() {
      this.insideTableData = this.setIndex(this.value)
      this._initM()
      this.resize()
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.insideTableData = this.setIndex(this.value)
      this._initM()
      this.resize()
    })
  }
}
</script>



